import React, { useState } from "react";
import { Container, Row, Col, Accordion } from "react-bootstrap";

const Faq = ({ faqData }) => {
  const [visibleItems, setVisibleItems] = useState(4);
  const [hasMore, setHasMore] = useState(true);

  const handleLoadMore = () => {
    setVisibleItems(visibleItems + 4);
    if (visibleItems + 4 >= faqData?.length) {
      setHasMore(false);
    }
  };

  return (
    <div className="faq_outline">
      <Container>
        <Row className="faq_row">
          <Col xxl={12} xl={12} lg={12} md={12} sm={12} className="p-lg-0">
            <div className="faq_rhs">
              <h2 className="faq_title">
                Resolve your unanswered queries effortlessly
              </h2>
              <div className="faq_acc_wrapper">
                <Accordion>
                  {faqData?.length > 0 &&
                    faqData?.slice(0, visibleItems)?.map((item, index) => (
                      <Accordion.Item key={index} eventKey={index.toString()}>
                        <Accordion.Header as="h3">
                          {/* <span style={{ marginRight: "8px" }}>
                            {index + 1}.
                          </span> */}
                          {/* numbers in the FAQ H3 headings, which is not recommended for semantic clarity, accessibility, and SEO. */}
                          <span>{item.question}</span>
                        </Accordion.Header>
                        <Accordion.Body>{item.answer}</Accordion.Body>
                      </Accordion.Item>
                    ))}
                  {faqData?.length > 4 && hasMore && (
                    <div
                      role="button"
                      tabIndex="0"
                      className="decenter_hero_sec_btn d-flex justify-content-center"
                      onKeyDown={(e) => {}}
                      onClick={handleLoadMore}
                    >
                      <button className="demo_btn">Load More</button>
                    </div>
                  )}
                </Accordion>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default Faq;
